.cards {
    background: #FFFFFF;
    border-radius: 36.3213px;
    margin-top: 120px;
    @media (max-width: 940px) {
        margin-top: 70px; }
    @media (max-width: 600px) {
        margin-top: 48px; }
    &.wrap {
        padding: 36px 50px;
        @media (max-width: 940px) {
            padding: 24px 32px; }
        @media (max-width: 600px) {
            padding: 24px;
            width: calc(100% - 48px); } }
    &:last-child {
        margin-bottom: 120px;
        @media (max-width: 600px) {
            margin-bottom: 48px; }
        @media (max-width: 940px) {
            margin-bottom: 70px; } }
    &__wrap {
        position: relative;
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin: 48px 0 0px;
        z-index: 0; } }
.photo {
    position: absolute;
    z-index: 2;
    padding: 9px 18px;
    background-color: rgba(57, 72, 80, 0.5);
    border-radius: 12px;
    color: #fff;
    top: 36px;
    left: 36px; }

.swiper {
    width: 100%;
    &-pagination {
        display: flex;
        justify-content: center;
        margin-top: 30px;
        &-bullet {
            display: block;
            margin: 0 15px;
            width: 295px;
            padding-bottom: 15.3%;
            border: 1px solid #68b8ff00;
            opacity: 1;
            border-radius: 13px;
            background-repeat: no-repeat;
            background-position: center -250px;
            background-size: cover;
            transition: .4s;
            opacity: 0.4;
            &:nth-child(1) {
                background-image: url("../../public/img/cards/riviera1.png");
                background-position: center;
                margin-left: 0; }
            &:nth-child(2) {
                background-image: url("../../public/img/cards/riviera2.png");
                background-position: center; }
            &:nth-child(3) {
                background-image: url("../../public/img/cards/riviera3.png");
                background-position: center; }
            &:nth-child(4) {
                background-image: url("../../public/img/cards/riviera4.png");
                background-position: center; }
            &:nth-child(5) {
                background-image: url("../../public/img/cards/riviera5.png");
                background-position: center;
                margin-right: 0; }
            &-active {
                opacity: 1; }
            @media ( max-width: 600px ) {
                &:nth-child(1) {
                    background: none;
                    background-color: #fff; }
                &:nth-child(2) {
                    background: none;
                    background-color: #fff; }
                &:nth-child(3) {
                    background: none;
                    background-color: #fff; }
                &:nth-child(4) {
                    background: none;
                    background-color: #fff; }
                &:nth-child(5) {
                    background: none;
                    background-color: #fff; } } } }


    &-wrapper {
        height: auto; } }
.card {
    width: 100%;
    text-align: center;
    margin: 0 auto;
    &__link {
        color: #CFAC6A;
        display: flex;
        align-items: center;
        img {
            margin-right: 15px; } }
    &__image {
        border-radius: 13px;
        img {
            width: 100%;
            border-radius: 13px; } } }
.arrows {
    position: absolute;
    display: flex;
    justify-content: space-between;
    z-index: 2;
    left: 0;
    right: 0;
    top: 34.5%;
    margin: 0 auto;
    width: 100%;
    padding: 0 36px;
    @media (max-width: 940px) {
        padding: 0 24px; }
    svg {
        width: 54px;
        @media (max-width: 940px) {
            width: 36px; } } }
