.swiper-button-disabled {
    opacity: 0.3;
    transition: .3; }
@media screen and (max-width: 940px) {
    .swiper-pagination-bullet {
        margin: 0 8px; } }

@media screen and (max-width: 600px) {
    .arrows {
        display: none; }
    .swiper {
        &-pagination {
            position: absolute;
            bottom: 24px;
            left: 0;
            right: 0;
            margin: 0 auto;
            z-index: 2;
            &-bullet, .investment &-bullet {
                width: 8px;
                height: 8px;
                border-radius: 50px;
                padding-bottom: 0;
                margin: 0 4px;
                opacity: .7;
                @for $var from 1 through 5 {
                    &:nth-child(#{$var}), .investment &:nth-child(#{$var}) {
                        background: #fff; } }
                &-active {
                    width: 24px;
                    opacity: 1; } } } } }

