.menu__mobile {
    position: fixed;
    z-index: 2;
    width: 100%;
    height: 100vh;
    transition: .5s;
    background-color: #fafafa;
    padding: 30px 0 70px;
    transform: translateX(-100vw);
    left: 0;
    top: 92px;
    font-size: 1.5rem;
    color: #1f2e36; }
